.hamburger span {
  position: absolute;
  width: inherit;
  height: 0.0937514649rem;
  background-color: #573314;
  transition: 0.3s;
}

.hamburger span:nth-child(1) {
  bottom: 0.5625087892rem;
}

.hamburger span:nth-child(2) {
  bottom: 0rem;
}

.hamburger.is-active span:nth-child(1) {
  -webkit-transform: translateY(0.1875029297rem) rotate(45deg);
  transform: translateY(0.1875029297rem) rotate(45deg);
}

.hamburger.is-active span:nth-child(2) {
  -webkit-transform: translateY(-0.3750058595rem) rotate(-45deg);
  transform: translateY(-0.3750058595rem) rotate(-45deg);
}

