  color: #333;
  text-align: center;
  font-family: "Noto Serif JP";
  font-size: 3.0000468757rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.3000046876rem;
}

.section-header__enTitle.section-header__enTitle--left {
  text-align: left;
}

.sp-nav {
  display: none;
}
@media screen and (max-width: 767px) {
  .sp-nav {
    z-index: 50;
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100dvh;
    transition: all 0.3s;
    transform: translate(100%);
    background-color: #F7F5F1;
    padding-left: 1.406271973rem;
    padding-right: 1.406271973rem;
    overflow-y: auto;
    padding-bottom: 1.6406506352rem;
  }
}

.sp-nav.is-active {
  transform: translate(0);
}

.sp-nav__items {
  display: flex;
  flex-direction: column;
  margin-top: 4.4063188487rem;
}

.sp-nav__item {
  color: #fff;
  font-weight: 700;
  font-size: 0.7031359865rem;
  line-height: 1.5333333333;
}

.sp-nav__item a {
  transition: all 0.3s;
}

@media (hover: hover) and (pointer: fine) {
  .sp-nav__item a:hover {
    opacity: 0.7;
  }
}
.sp-nav__item:not(:first-child) {
  margin-top: 0.9375146487rem;
}

.sp-nav__service-items {
  margin-top: 0.7500117189rem;
  margin-left: 0.9375146487rem;
}

.sp-nav__service-item {
  font-size: 0.5625087892rem;
  font-weight: 500;
  line-height: 2.5;
}

.sp-nav__contact {
  margin-top: 3.890685792rem;
  text-align: center;
  position: relative;
}

.top-feature {
  margin-top: 5.9063422866rem;
}

.top-feature__section-header {
  text-align: center;
}

.top-feature__bg {
  margin-top: 2.812543946rem;
  background: linear-gradient(90deg, #F7F5F1 0%, #F7F5F1 62%, transparent 62%, transparent 100%);
  padding-top: 3.7500585947rem;
  padding-bottom: 5.0157033704rem;
  position: relative;
  z-index: 10;
}

.top-feature__item:first-child {
  margin-top: 0rem;
}

.top-feature__item {
  display: flex;
  align-items: end;
  margin-right: calc(50% - 50vw);
  margin-top: 6.5626025407rem;
}
@media screen and (max-width: 767px) {
  .top-feature__item {
    flex-direction: column;
  }
}

.top-feature__text-wrapper {
  background-color: #fff;
  width: 59%;
  padding: 3.9375615244rem 1.8750292973rem 2.3437866217rem;
  position: relative;
  margin-right: -3.7500585947rem;
  z-index: 2;
}
@media screen and (max-width: 767px) {
  .top-feature__text-wrapper {
    width: 100%;
    margin-right: 0rem;
  }
}

.top-feature__number {
  color: rgba(97, 148, 143, 0.57);
  font-family: "Noto Serif JP";
  font-size: 6.0000937515rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  position: absolute;
  top: -4.6875732433rem;
  left: 2.9531711433rem;
}

.top-feature__title {
  color: #333;
  font-family: "Noto Sans JP";
  font-size: 1.6875263676rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.top-feature__text {
  margin-top: 1.406271973rem;
  color: #333;
  font-family: "Noto Sans JP";
  font-size: 0.9375146487rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.0375005859rem;
}

.top-feature__btn {
  margin-top: 1.5937749027rem;
}

.top-feature__img {
  position: relative;
  aspect-ratio: 922/538;
  width: 59%;
  margin-bottom: 1.1718933108rem;
}
@media screen and (max-width: 767px) {
  .top-feature__img {
    width: 100%;
  }
}

.top-feature__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-feature__btn-brown {
  background-color: #E2DBCF;
  padding-top: 4.6875732433rem;
  padding-bottom: 5.625087892rem;
  margin-right: calc(50% - 50vw);
  position: relative;
  z-index: 2;
  margin-top: -1.6406506352rem;
}

.top-first {
  margin-top: 5.625087892rem;
}

.top-first__inner {
  display: flex;
  gap: 2.812543946rem;
}
@media screen and (max-width: 767px) {
  .top-first__inner {
    flex-direction: column;
  }
}

.top-first__text-wrapper {
  width: 60%;
}
@media screen and (max-width: 767px) {
  .top-first__text-wrapper {
    width: 100%;
  }
}

.top-first__text {
  margin-top: 3.2344255379rem;
  color: #333;
  font-family: "Noto Sans JP";
  font-size: 0.8437631838rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.0337505274rem;
}

.top-first__btn {
  margin-top: 1.8750292973rem;
}

.top-first__img {
