.top-reservation {
  padding-top: 5.625087892rem;
  padding-bottom: 5.625087892rem;
  position: relative;
}

.top-reservation::after {
  background-image: url(../images/common/top-reservation-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.top-reservation__wrapper {
  background: rgba(255, 255, 255, 0.74);
